.main-seti {

    @include flex( column, nowrap, space-around, center, center );
    gap: 10px;

    /*   *   *   *   *   *   *   *   *   *   */

    padding: $std-padding;
    width: 100%;

    /*   *   *   *   *   *   *   *   *   *   */

    .elem {

        @include flex();

        width: 100%;
        margin: $std-margin;
        padding: $std-padding;

        /*   *   *   *   *   *   *   *   */

        input {

            margin: 10px 0;
            padding: $std-padding;
            width: 66%;
        }

        span {
            font-family: 'Courier New', Courier, monospace;
            text-align: center;
            font-size: 18px;
        }
    }

    /*   *   *   *   *   *   *   *   */

    a.next {

        visibility: collapse;

        &.__isVisible {
            visibility: visible;
        }

        /*   *   *   *   *   *   *   *   */

        background-color: #00FF00;
        display: block;
        width: 50%;

        text-align: center;
        font-size: x-large;
        font-weight: 600;

        flex-grow: 1;
        padding: $std-padding;
    }
}