.board {

    /*   *   *   *   *   *   *   *   *   *   */

    @include theme( $color-font, $color-background );
    @include transition();

    /*   *   *   *   *   *   *   *   *   *   */

    display: grid;
    gap: 5px;
    
    display: grid;
    gap: 4px;

    height: min( 90vw, 90vh );
    width: min( 90vw, 90vh );

    max-height: 512px;
    max-width: 512px;

    /*   *   *   *   *   *   *   *   *   *   */
    
    .field {

        color: $color-font;
        background-color: $color-background;
        position: relative;

        &::before {
            content: " ";
            position: absolute;
            z-index: 10;
            left: 50%;
            top: 50%;

            font-size: clamp( 24px, 10vw, 52px );
            font-weight: 300;
            
            text-align: center;
            text-transform: uppercase;
            transform: translateX( -50% ) translateY( -50% );
        }

        &.__O::before { content: "\25EF"; }
        &.__X::before { content: "\2717"; }
    }
}