.topInfo {

    /*   *   *   *   *   *   *   *   *   *   */

    width: 100%;
    position: absolute;
    left: 0;
    top: 0;

    padding: $std-padding;
    font-size: 40px;

    /*   *   *   *   *   *   *   *   *   *   */

    .home { color: #337733; }
    .game { color: #FF2200; }

    /*   *   *   *   *   *   *   *   *   *   */
    
    svg { @include transition(); }
    .home svg:hover { transform: rotate( 10deg ) scale( 1.15 ); }
    .game svg:hover { transform: rotate(-45deg ) scale( 1.05 ); }
}