.main-home {

    @include flex( column, nowrap, space-around, center, center );
    gap: 20px;

    /*   *   *   *   *   *   *   *   *   *   */

    padding: $std-padding;
    width: 100%;

    /*   *   *   *   *   *   *   *   *   *   */

    .elem {

        width: 100%;
        margin: $std-margin;
        padding: $std-padding;

        /*   *   *   *   *   *   *   *   */

        .choice {

            @include flex( row, nowrap, center, center, center );
            gap: 20px;

            /*   *   *   *   *   *   */

            button {

                font-size: x-large;

                flex-grow: 1;
                margin: $std-margin 0;
                padding: 25px $std-padding;

                &.__type-a { background-color: #0075e2; }
                &.__type-b { background-color: #e78166; }
            }
        }
    }

    /*   *   *   *   *   *   *   *   */

    a.next {

        visibility: collapse;

        &.__isVisible {
            visibility: visible;
        }

        /*   *   *   *   *   *   *   *   */

        background-color: #00FF00;
        display: block;
        width: 50%;

        text-align: center;
        font-size: x-large;
        font-weight: 600;

        flex-grow: 1;
        padding: $std-padding;
    }
}